.backdrop {
  z-index: 200;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(2px);
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  transition: opacity .25s linear, visibility .25s linear;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

